  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    margin: 60px 400px 60px 400px;
  }

  h3 {
    padding-left: 20px;
    text-align: center;
  }

  .margin-right-lg {
    margin-right: 20px;
  }

  .right {
    text-align: right;
  }

  .error-message {
    font-size: 30px;
    color: red;
    white-space: pre;
    text-align: center;
  }

  .encrypted-text-block {
    word-wrap: break-word;
    white-space: normal;
    padding: 20px;
  }

  .encryption-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 25px;
    column-gap: 70px;
  }

  .key-input {
    width: 40px;
    display: inline;
