  .key-container {
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
    align-items: center;
  }

  .decrypted-block {
    word-wrap: break-word;
    text-align: left;
    white-space: pre-wrap;
    font-size: 18px;
  }
</style>
